import React, { Component, Fragment } from "react";

class FormBuilderInput extends Component {
    render(){
        return (
            <Fragment>
                <div className="formElement">
                    <label>Element Name: </label>
                    <input type="text" name="inputName" />
                </div>
                <div className="formElement">
                    <label>Element Description: </label>
                    <input type="text" name="inputDescription" />
                </div>
                <div className="formElement">
                    <label>Element Type: </label>
                    <select name="inputElement">
                        <option selected value="input">Text</option>
                    </select>
                </div>
                <div className="formElement">
                    <label>Required:</label>
                    <label>No:<input type="checkbox" name="required" value="false" checked /></label>
                    <label>Yes:<input type="checkbox" name="required" value="true" /></label>
                </div>
            </Fragment>
        )
    }
}
export default FormBuilderInput;